}

.photo-card:hover {
  filter: grayscale(0);
  box-shadow: 0 5px 35px #0000001a;
}

.photo-card:hover:before, .photo-card:hover:after {
  content: "";
  z-index: -1;
  background: #7490ff;
  border-radius: 5px;
  width: 270px;
  height: 200px;
  animation: 1s infinite clockwise;
  display: block;
  position: absolute;
}

.photo-card:hover:after {
  background: #6cf41e;
  animation: 2s infinite counterclockwise;
}

@keyframes clockwise {
